/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Yanda's Lab","subtitle":"The existence of this blog fills you with determination.","author":{"name":"Yanda Huang"},"disqusShortname":"","url":"https://yanda.rocks"}},"markdownRemark":{"id":"/home/yanda/Development/yodahuang.github.io/src/pages/articles/2018-04-27---DDPG-vs-A2C/index.md absPath of file >>> MarkdownRemark","html":"<p><a href=\"https://arxiv.org/abs/1509.02971\">Deep Deterministic Policy Gradient</a> algorithm and <a href=\"https://arxiv.org/abs/1602.01783\">Advantage Actor Critic</a> algorithm both have two networks, namely Actor and Critic network for training the agent. When I read the papers, I was kinda confused about their difference. Here is some of my thoughts.</p>\n<p>DDPG aims to extend Deep Q Network to continuous action space. In DQN, to pick an action you need to go throught the network and calculate <code class=\"language-text\">argmax</code>, which is infeasible for continuous action space. To fix this problem, DDPG introduce another actor network to pick the “best action”. So critic network is the main network, while the actor network is the sub network.</p>\n<p>A3C is a improvement on the Reinforce algorith. It adds a baseline to the algorithm to make it more robust. The baseline is calculated using a critic network. Note here the critic network is not a Q network, since its input is a state instead of state-action pair. Here the actor network is the main network and the critic one is the sub network.</p>\n<p>The network weights are also updated differently.</p>\n<p>DDPG updates critic network by Temporal Difference (TD) update. The actor network is updated based on the fact that the good state-action pair should be visited more often. Since it’s deterministic, it does not to some over something in loss function.</p>\n<p>A2C updates actor network by awarding good actions, using Monte Carlo method minus baseline as an estimatte of Q function. The critic network is updated by making the baseline better.</p>","fields":{"tagSlugs":["/tags/ddpg/","/tags/a-3-c/"],"date":"2018-04-27T20:32:37.000Z"},"frontmatter":{"title":"Comparision between DDPG and A2C","tags":["DDPG","A3c"],"date":"2018-04-27T20:32:37.000Z","description":"DDPG and A2C both use the Actor-Critic structure. What are the differences between them?"}}},"pathContext":{"slug":"/articles/DDPG-vs-A2C/"}}

/***/ })

